

















import Vue from "vue";
import { DialogAction } from "../types/dialog-action";
import { Component, Prop } from "vue-property-decorator";

@Component({})
export default class MessageDialog extends Vue {
  @Prop({ default: "" })
  message!: string;

  @Prop({ default: [] })
  actions!: DialogAction[];

  resolve(value: any) {
    this.$emit("dialog-result", value);
  }
}
