



























import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import { DialogOptions } from "../types/dialog-options";

@Component({})
export default class Dialog extends Vue implements DialogOptions {
  private ref!: any;

  @Prop()
  component!: any;

  @Prop()
  props!: any;

  @Prop()
  title!: string;

  @Prop()
  height?: string;

  @Prop()
  width?: string;

  @Prop({ default: () => [] })
  rules!: string[];

  get styles() {
    return {
      "--height": this.height,
      "--width": this.width,
    };
  }

  close(data?: any) {
    this.ref.close(data);
  }
}
